@import "../../scss/main.scss";

.wrapper {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  padding: {
    left: 28px;
    top: 15px;
  }
  p {
    color: $black;
  }
  .deposit-header {
    display: flex;
    width: calc(100vw - 150px);
    flex-flow: row wrap;
    justify-content: start;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    color: $black;
    margin: {
      bottom: 30px;
    }
    .link {
      color: rgb(191, 197, 209);
      margin: {
        right: 30px;
      }
      font-size: 0.8925rem;
      text-transform: uppercase;
      padding: {
        left: 37px;
        right: 37px;
        bottom: 10px;
        top: 1px;
      }
    }
    .active-item {
      color: $btn-blue;
      border-bottom: 2px solid $btn-blue;
    }
  }
  .content {
    display: flex;
    flex-flow: row nowrap;
    justify-content: start;
    align-items: start;
    width: 100vw;
    .deposit-settings {
      display: flex;
      flex-flow: column nowrap;
      padding: 20px;
      background-color: white;
      border: 1px solid rgb(229, 230, 235);
      width: calc(100vw - 520px);
      h1 {
        font-size: 1.6em;
        text-align: start;
        color: $black;
      }

      .slider {
        width: 100%;
        margin: {
          top: 25px;
          bottom: 60px;
        }
      }
      .buy-currency {
        display: flex;
        flex-flow: column wrap;
        justify-content: center;
        align-items: center;
        background-color: white;
        border: 1px solid rgb(229, 230, 235);
        margin: {
          top: 30px;
        }
        .title {
          margin: {
            top: 30px;
          }
        }
        .buy-currency-options {
          display: flex;
          flex-flow: column wrap;
          justify-content: center;
          align-items: center;
          .title {
            font-size: 1.5em;
          }
          .select,.inp {
            select, input {
              width: 100%;
            }
          }
        }
      }
    }
    .summ-settings {
      display: flex;
      flex-flow: row nowrap;
      justify-content: start;
      align-items: center;
      margin: {
        top: 30px;
      }
      .currency {
        display: flex;
        .icon {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 40px;
          width: 40px;
          margin-left: 10px;
          cursor: pointer;
          font-weight: 500;
          color: inherit;
          transition: all 0.3s ease 0s;
          border-radius: 50%;
          border-width: 1px;
          border-style: solid;
          border-color: rgb(229, 230, 235);
          border-image: initial;
          background: inherit;
          &:hover,
          &:active {
            border-color: rgb(229, 230, 235);
            border-image: initial;
            background: $btn-blue;
            color: white;
          }
        }
      }
      .input-summ {
        input {
          width: 25vw;
          border: 2px solid $input-hover;
          margin: {
            left: 15px;
            right: 15px;
          }
        }
      }
    }
    .deposit-info {
      display: flex;
      flex-flow: column nowrap;
      align-items: flex-start;
      h3 {
        margin: {
          bottom: 20px;
        }
        color: $black;
      }
      h2 {
        font-size: 1.6rem;
        color: $btn-blue;
        span {
          color: $black;
        }
      }
      .profits {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        .profit-percent {
          h2 {
            font-weight: 600;
            font-size: 18px;
          }
        }
        .profit-in-period {
          p {
            font-weight: 700;
            font-size: 12px !important;
            color: rgb(218, 218, 218);
          }
        }
        .profit-percent,
        .profit-in-period,
        .invest {
          height: 27vh;
          width: 19.5vw;
          display: flex;
          flex-flow: column nowrap;
          align-items: flex-start;

          border-right: 1px solid rgb(218, 218, 218);
          padding: {
            right: 25px;
          }
          p {
            text-align: start;
            width: 17vw;
            font-size: 0.9rem;
          }
        }
        .profit-in-period {
          padding: {
            left: 25px;
          }

          h1 {
            margin: 0;
            color: rgb(47, 128, 237) !important;
            font-weight: 800 !important;
            font-size: 24px !important;
          }
          p {
            color: rgb(218, 218, 218);
            font-weight: 700;
            span {
              color: $btn-blue;
            }
          }
        }
        .invest {
          border-right: none;
          p {
            font-size: 14px;
            color: rgb(0, 0, 0);
          }
          padding: {
            left: 25px;
          }
          .btn {
            button {
              @extend %shared-btn;
              width: 170px;
              height: 50px;
            }
          }
        }
      }
    }
    .deposit-variation {
      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      align-items: center;
    }
  }
}


@media only screen and (min-device-width: 480px) and (max-device-width: 1024px) {
  .wrapper {
    .content {
      flex-flow: row nowrap !important;
    }
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .wrapper {
    flex-flow: column wrap;
    padding: {
      left: 0px;
      top: 15px;
    }
    .deposit-header {
      width: 100%;
      .link {
        font-size: 0.6925rem;
        text-transform: uppercase;
        padding: {
          left: 9px;
          right: 9px;
          bottom: 0px;
          top: 1px;
        }
      }
    }
    .content {
      flex-flow: column wrap;
      justify-content: start;
      align-items: start;
      width: 100%;
      .deposit-settings {
        flex-flow: column wrap;
        padding: 10px;
        width: 100%;
        h1 {
          font-size: 1.4em;
          text-align: center;
        }
        .slider {
          margin: {
            bottom: 25px;
          }
        }
      }
      .summ-settings {
        flex-flow: column wrap;
        .currency,
        .input-summ {
          margin: {
            top: 10px;
          }
        }
        .input-summ {
          input {
            width: 100%;
            margin: {
              left: 0px;
              right: 0px;
            }
          }
        }
      }
      .deposit-info {
        .profits {
          flex-flow: column wrap;
          .profit-percent,
          .profit-in-period,
          .invest {
            width: 100%;
            height: unset;
            flex-flow: column wrap;
            border-right: unset;
            border-bottom: 1px solid rgb(218, 218, 218);
            padding: {
              right: 0px;
              top: 15px;
              bottom: 15px;
            }
            p {
              text-align: start;
              width: 100%;
              font-size: 1.1rem;
            }
          }
          .profit-in-period {
            padding: {
              left: 0px;
            }

            h1 {
              text-align: start;
            }
          }
          .invest {
            p {
              text-align: start;
            }
            padding: {
              left: 0px;
            }
          }
        }
      }
      .deposit-variation {
        width: 100%;
        margin-top: 20px;
      }
    }
  }
}
