@import "../../scss/main.scss";

.wrapper {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  padding: {
    left: 28px;
    top: 15px;
  }
}
.wallet-header {
  display: flex;
  width: calc(100vw - 150px);
  flex-flow: row wrap;
  justify-content: start;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  color: $black;
  margin: {
    bottom: 30px;
  }
  .link {
    color: rgb(191, 197, 209);
    margin: {
      right: 30px;
    }
    font-size: 0.8925rem;
    text-transform: uppercase;
    padding: {
      left: 37px;
      right: 37px;
      bottom: 10px;
      top: 1px;
    }
  }
  .active-item {
    color: $btn-blue;
    border-bottom: 2px solid $btn-blue;
  }
}
.content {
  display: flex;
  flex-flow: column nowrap;
  justify-content: start;
  align-items: start;
  width: 100vw;
  background-color: white;
  border: 1px solid rgb(229, 230, 235);
  margin: {
    bottom: 30px;
  }
  padding: {
    top: 20px;
    left: 30px;
    bottom: 30px;
    right: 30px;
  }
  .title {
    h1 {
      font-weight: 600;
      font-size: 1.7em;
      margin-bottom: 30px;
      color: rgb(79, 79, 79);
    }
  }
  h1,
  h4,
  h3 {
    font-size: 1.19em;
    text-align: start;
    color: $black;
  }
  .payment-settings {
    display: flex;
    flex-flow: row nowrap;
    width: calc(100vw - 250px);
    justify-content: start;
    align-items: center;
    margin: {
      top: 30px;
    }
    .currency {
      display: flex;
      .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        width: 40px;
        margin-left: 10px;
        cursor: pointer;
        font-weight: 500;
        color: inherit;
        transition: all 0.3s ease 0s;
        border-radius: 50%;
        border-width: 1px;
        border-style: solid;
        border-color: rgb(229, 230, 235);
        border-image: initial;
        background: inherit;
        &:hover,
        &:active {
          border-color: rgb(229, 230, 235);
          border-image: initial;
          background: $btn-blue;
          color: white;
        }
      }
    }
    .input-summ {
      margin: {
        left: 10px
      };
      input {
        width: 35vw;
        color: black;
        background: white;
        border: 1px solid rgb(229, 230, 236) !important;
        &:hover {
          border-width: 2px !important;
          border: 2px solid #37b076 !important;
          cursor: pointer;
        }
      }
    }
  }
  .cards {
    padding: {
      top: 30px;
    }
  }
  .widthraw-request {
    padding: {
      left: 30px;
    };
  }
}


@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { 
  .payment-settings {
    flex-flow: column nowrap !important;
  }
} 

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) { 
  .wrapper {
    padding: {
      left: 0px;
    }
  }
  .wallet-header {
    width: 100%;
    margin: {
      bottom: 30px;
    }
    .link {
      font-size: 0.6925rem;
      text-transform: uppercase;
      padding: {
        left: 9px;
        right: 9px;
        bottom: 0px;
        top: 1px;
      }
    }
  }
  .content {
    width: 100%;
    background-color: white;
    border: 1px solid rgb(229, 230, 235);
    margin: {
      bottom: 30px;
    }
    padding: {
      top: 10px;
      left: 10px;
      bottom: 10px;
      right: 10px;
    }
    .payment-settings {
      width: 100%;
      
      justify-content: center;
      margin: {
        top: 30px;
      }
      h3 {
        text-align: center;
        margin: {
          right: 8px;
        };
      }
      .currency, .input-summ {
        margin: {
          top: 10px;
        };
      }
      .input-summ {
        input {
          width: 100%;
        }
      }
    }
    .widthraw-request {
      padding: {
        left: 0px;
      };
      text-align: center;
    }
  }
  .cards { 
    width: 100%;
    h4 {
      text-align: center;
    }
  }
}