@import "../../scss/main.scss";

.wrapper {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

.title {
  @extend %shared-title;
  margin: {
    bottom: $gutter !important;
  }
}

.btn {
  @extend %shared-btn;
}

.link {
  @extend %shared-links;
}

.icon {
  @extend %shared-icon;
}

.input {
  display: flex;
  flex-flow: column nowrap;
  margin: {
    bottom: 40px;
  }
  & > .link {
    align-self: flex-start;
  }
}

.btn-block {
  div {
    margin: {
      top: 10px;
    }
  }
}

.register {
  margin: {
    left: 0px;
  }
}


@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .wrapper {
    h1 {
      font-size: 22px;
    }
    input {
      width: 250px !important;
    }
  }
}