@import "../../scss/vars";
@import "../../scss/fonts";

.wrapper {
  height: 55px;
  position: fixed;
  top: 0;
  z-index: 999999;
  width: 100vw;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  background-color: white;
  border-bottom: 1px solid $border-header;

  .icon {
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 1px solid $border-header;
    img {
      width: 32px;
      height: 32px;
    }
  }

  .left-menu-items,
  .right-menu-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 30px;
    border-right: 1px solid $border-header;
  }

  .right-menu-items {
    border-left: 1px solid $border-header;
    border-right: none;
  }

  i {
    line-height: 0.9;
    font-size: 1.15em;
    margin: {
      right: 0.8rem;
    }
    color: $btn-blue;
  }

  a,
  p {
    text-transform: uppercase;
    font-size: 10px;
    color: $btn-blue;
    font-weight: bold;
  }
}

// sign inned

.page-name,
.balance,
.currency,
.mail,
.settings {
  display: flex;
  flex-flow: row nowrap;
  padding: 20px;
  align-items: center;
  justify-content: center;
  max-width: 55px;
  border-right: 1px solid $border-header;
  text-transform: uppercase;
}

.balance,
.page-name {
  justify-content: unset;
  max-width: unset;
}

.currency {
  border-left: 1px solid $border-header;
}

.currency i,
.mail i,
.settings i,
.menu-dropdown i {
  cursor: pointer;
  font-size: 15px;
  margin-right: 0px;
  color: $black;
  &:hover {
    color: $btn-blue;
  }
}

.balance p {
  font-size: 15px;
  font-weight: bold;
}

.menu-wrapper {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  height: 55px;
  max-width: 300px;
}
.mobile-mail {
  display: none !important;
}

.menu {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 100%;
}
.user-info {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  p {
    color: red;
    font-size: 8px;
    margin: {
      bottom: 0;
    }
  }
  span {
    font-size: 16px;
  }
  text-align: left;

  .user-info-text {
    padding: 0 35px;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .page-name,
  .mail,
  .settings {
    display: none !important;
  }
  .mobile-mail {
    display: flex !important;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .page-name,
  .mail,
  .settings {
    display: flex;
  }
  .balance {
    border-right: none;
  }
  .menu-wrapper {
    padding: 0 20px;
    img {
      display: none !important;
    }
    .user-info-text {
      padding: 0 10px;
    }
  }
  .left-menu-items,
  .right-menu-items {
    padding: 0px 20px !important;
    border-right: unset !important;
  }
}
