@import "../../../../scss/main.scss";


.fileInput {
  display: none;
}

.sidebar {
  background: white;
  align-self: start;
  border: 1px solid $border-header;
  padding: 12px;
  flex-grow: 0.5;
  max-width: 200px;
  min-width: 180px;
  margin: {
    left: 0;
    right: 40px;
  }
  .balance {
    display: flex;
    align-items: start;
    margin: {
      bottom: 10px;
    }
  }
  .current-balance {
    display: flex;
    flex-flow: row nowrap;
    align-items: start;
    color: black;
    font-size: 35px;
    font-weight: 800;
    margin: {
      bottom: 40px;
    }
  }
  .balance-info {
    display: flex;
    flex-flow: column nowrap;
    .item {
      text-align: start;
      display: flex;
      flex-flow: row nowrap;
      p {
        font-size: 10px;
        color: grey;
        letter-spacing: 1px;
        margin-bottom: 5px;
        span {
          font-size: 12px;
          color: $black;
        }
      }
      i {
        font-size: 11px;
      }
    }
  }
  span {
    align-self: start;
  }
  .btn {
    margin: {
      top: 15px;
    }
    button {
      width: 95%;
      max-width: 240px;
      height: 40px;
      background-color: $btn-blue;
      box-shadow: rgba(47, 128, 237, 0.6) 0px 3px 10px;
      color: white;
      font-family: Montserrat, arial;
      font-size: 10px;
      font-weight: 900;
      text-transform: uppercase;
      cursor: pointer;
      padding: 5px 5px 5px 20px;
      border-radius: 30px;
      outline: none;
      transition: all 0.03s ease 0s;
      &:hover {
        background-color: #2f80ed;
      }
    }
    .btn-red {
      background-color: rgb(251, 38, 38);
      &:hover {
        background-color: rgb(251, 38, 38);
      }
    }
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .sidebar {
    align-self: center;
    margin: {
      right: 0px;
    }
  }
}
