@import "../../../../scss/main.scss";

.user-profile-header {
  display: flex;
  width: calc(100vw - 150px);
  flex-flow: row wrap;
  flex-flow: row wrap;
  justify-content: start;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  color: $black;
  margin: {
    bottom: 30px;
  }
  .link {
    color: rgb(191, 197, 209);
    margin: {
      right: 30px;
    }
    font-size: 0.8925rem;
    text-transform: uppercase;
    padding: {
      left: 37px;
      right: 37px;
      bottom: 10px;
      top: 1px;
    }
  }
  .active-item {
    color: $btn-blue;
    border-bottom: 2px solid $btn-blue !important;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .user-profile-header {
    width: 100%;
    margin: {
      bottom: 30px;
    }
    border-bottom:unset;
    .link {
      font-size: 0.6925rem;
      text-transform: uppercase;
      border-bottom: 1px solid rgba(0, 0, 0, 0.12);
      padding: {
        left: 9px;
        right: 9px;
        bottom: 0px;
        top: 1px;
      }
    }
  }
}
