@import "../../../../scss/main.scss";
@import "../../../../scss/shared";

.settings {
  display: flex;
  flex-flow: column nowrap;
  padding: 25px;
  background-color: white;
  border: 1px solid rgb(229, 230, 235);
  width: calc(100vw - 350px);
  justify-content: center;
  align-items: start;
  h1 {
    font-size: 1.6em;
    text-align: start;
    color: $black;
  }
}
.input {
  display: flex;
  margin: {
    top: 25px;
  }
}
.btn {
  @extend %shared-btn;
  margin: {
    top: 40px;
  }
}

@media only screen and (min-device-width: 480px) and (max-device-width: 1024px) {
  .settings {
    .input {
      input {
        width: 520px !important;
      }
    }
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .settings {
    margin: {
      top: 40px;
    }
    padding: 25px;
    width: 100%;
    justify-content: center;
    align-items: start;
    h1 {
      font-size: 1.4em;
    }
  }
  .input {
    display: flex;
    width: 100%;
    margin: {
      top: 25px;
    }
    input {
      width: 100% !important;
    }
  }
  .btn {
    width: 165%;
  }
}
