@import "../../scss/main.scss";

.wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: start;
  background: $white;
  height: 100%;
  padding: {
    top: 40px;
    left: 30px;
    bottom: 40px;
    right: 40px;
  }
}

.select,
.inp,
.button {
  margin: {
    top: 30px;
  }
}

.blueBtn { 
  @extend %shared-btn;
  margin: {
    top: 30px;
  }
}



.main-content {
  flex-grow: 1;
  height: calc(100vh - 200px);
  margin: {
    right: 2.5rem;
  }
  border: 1px solid $border-header;
  background: white;
  .select {
    div {
      width: 520px;
    }
  }
}
.main-content-chart {
  height: 100%;
}
.buy-currency {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 50px;
  padding-left: 40px;
  padding-right: 40px;
  // border: 1px solid red;
  // display: flex;
  // box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  // flex-flow: column nowrap;
