@import "../../../../scss/main.scss";

.settings {
  display: flex;
  flex-flow: column nowrap;
  padding: 25px;
  background-color: white;
  border: 1px solid rgb(229, 230, 235);
  width: calc(100vw - 350px);
  justify-content: center;
  align-items: start;
  h1 {
    font-size: 1.6em;
    text-align: start;
    color: $black;
  }
}
.title {
  font-size: 1.6em;
  padding-left: 0;
  margin: {
    top: 30px !important;
  }
  color: $black;
}

.description,
.title {
  text-align: left;
}

.description {
  width: 70%;
  font-size: 12px;
  color: rgb(119, 123, 144) !important;
  margin: {
    bottom: 20px !important;
  }
}
.btn {
  @extend %shared-btn;
  margin: {
    top: 40px;
  }
}

.image {
  max-width: 200px;
  height: auto;
}

@media only screen and (min-device-width: 480px) and (max-device-width: 1024px) {
  .btn-block {
    .btn {
      width: unset;
    }
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .title {
    font-size: 1.2em;
    margin: {
      top: 0px !important;
    }
  }
  .settings {
    margin: {
      top: 40px;
    }
    padding: 25px;
    width: 100%;
    justify-content: center;
    align-items: start;
    h1 {
      font-size: 1.4em;
    }
  }
  .btn {
    width: 180px;
    margin: {
      bottom: 20px;
    }
  }
}
