.cards-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: start;
  align-self: start;
  p {
    margin: {
      bottom: 0;
    }
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .cards-container {
    justify-content: center;
  }
}
