@import "../../scss/main.scss";

.container {
  .content {
    display: flex;
    flex-flow: column nowrap;
    justify-content: start;
    width: 240px;
    cursor: pointer;
    margin-bottom: 20px;
    border-width: 2px;
    border-style: solid;
    border-color: rgb(47, 128, 237);
    border-image: initial;
    transition: all 0.3s ease 0s;
    background: white;
    padding: 25px;
    border-radius: 10px;
    margin: {
      bottom: 15px;
      left: 50px;
    }
    .card-header,
    .range,
    .terms {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      margin: {
        bottom: 10px;
      }
    }
    .card-header {
      justify-content: space-between;
      h1 {
        font-weight: 600;
        color: $black;
        font-size: 18px;
        margin: 0;
        text-align: left;
      }
      .percent {
        width: 35px;
        text-align: right;
      }
    }
    .range,
    .terms {
      p {
        font-weight: 600;
        font-size: 11px;
        color: rgb(147, 193, 255);
        margin: 0;
        text-align: left;
      }
    }
  }
  .active-card {
    background: $btn-blue;
    .card-header {
      h1 {
        color: white !important;
      }
    }
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { 
  .container {
    width: 100%;
    .content {
      justify-content: center;
      align-items: center;
      width: auto;
      margin: {
        top: 20px;
      };
      padding: 13px;
      margin: {
        left: 0px;
      }
      .card-header {
        justify-content: space-between;
        h1 {
          margin: {
            right: 10px
          };
        }
        .percent {
          width: 35px;
          text-align: right;
        }
      }
      .range,
      .terms {
        p {
          font-weight: 600;
          font-size: 11px;
          color: rgb(147, 193, 255);
          margin: 0;
          text-align: left;
        }
      }
    }
    .active-card {
      background: $btn-blue;
      .card-header {
        h1 {
          color: white !important;
        }
      }
    }
  }
}