@import "../../scss/main.scss";

.wrapper {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  background-color: $white;

  p {
    font-size: 12px;
    font-weight: 400;
  }
  .accept-rules {
    display: flex;
    flex-flow: row nowrap;
    p {
      text-align: left;
    }
    margin: {
      bottom: 40px;
    }
  }

  .rules {
    width: 490px;
  }
}

.title {
  @extend %shared-title;
  margin: {
    bottom: $gutter !important;
    top: $gutter !important;
  }
}

.btn {
  @extend %shared-btn;
}

.link {
  @extend %shared-links;
}

.icon {
  @extend %shared-icon;
}

.input {
  margin: {
    bottom: 40px;
  }

  .input-with-label,
  .btn-block,
  .re-password {
    margin: {
      bottom: 20px;
    }
    p {
      color: rgba(0, 0, 0, 0.6);
      text-align: left;
      padding: {
        left: 1.8rem;
      }
    }
  }

  .btn-block {
    div {
      margin: {
        top: 10px;
      }
    }
  }

  .register {
    margin: {
      left: 0px;
    }
  }

  .checkbox {
    display: flex;
    align-items: center;
    margin: {
      right: 20px;
    }
    label:before {
      border: 3px solid rgb(151, 151, 151);
    }
    label:after {
      color: $btn-blue !important;
    }
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
  .wrapper {
    h1 {
      font-size: 22px;
    }
    input {
      width: 250px !important;
    }
    .accept-rules {
      width: 100%;
    }
  }
}