@import "../../../../scss/main.scss";

.modal {
  position: absolute;
  left: calc(50% - 200px);
  top: calc(50% - 100px);
  width: 400px;
  height: 200px;
  background: '#fff';
  border: '2px solid #000';
}

.card {
  width: 290px;
  cursor: pointer;
  position: relative;
  display: flex;
  margin-right: 20px;
  margin-bottom: 5px;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  flex-direction: column;
  background: $white;
  border-width: 2px;
  border-style: solid;
  border-color: $white;
  border-image: initial;
  border-radius: 5px;
  padding: 20px;
  transition: all 0.3s ease 0s;
  &:hover {
    border-width: 2px;
    border-style: solid;
    border-color: $link-blue;
    border-image: initial;
  }
  .card-title {
    margin-top: 20px;
    font-weight: 600;
    font-size: 1.19em;
    color: $black;
    small {
      font-size: 0.6em;
    }
  }
  .disabled {
    img {
      opacity: 0.3 !important; 
    }
  }
  img {
    filter: grayscale(0%);
    opacity: 1;
    width: 100% !important;
    height: 170px !important;
    object-fit: scale-down;
    max-width: 70% !important;
    margin: 0px auto;
  }
}


@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { 
  .card {
    width: 100%;
    margin: {
      right: 0px;
      bottom: 10px;
    };
    padding: 0px;
  }
}